<template>
  <div>
    <!-- 二级路由出口 -->
    <transition enter-active-class="animate__animated animate__bounceInDown">
      <router-view></router-view>
    </transition>
    <footer>
      <router-link to="/index/home" active-class="active">首页</router-link>
      <router-link to="/index/order" active-class="active">订单</router-link>
      <router-link to="/index/mine" active-class="active">我的</router-link>
    </footer>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    methods: {},
    watch: {},
    mounted() {
      console.log('大首页进来了');
    },
    beforeDestroy() {
      console.log('大首页销毁了');
    }
  }

</script>
<style>
footer{
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  width: 100vw;
}
footer a{
  flex: 1;
  text-align: center;
  line-height: 0.5rem;
  border:1px solid pink
}
.active{
  background: turquoise;
  color: purple;
}
</style>



